.TuduCounter {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: montserrat;
    margin-top: 30px;
    box-sizing: border-box;
    height: 60px;
    color: var(--text-color-white);
    font-size: 24px;
    border-radius: 1rem;
    border: none;
    position: fixed;
    transition: 0.1s;
    z-index: 2;
}

.TuduCounter::after {
content: '';
width: 100%;
height: 100%;
background-image: var(--primary-color-5);
filter: blur(15px);
z-index: -1;
position: absolute;
left: 0;
top: 0;
}

.TuduCounter:hover {
transform: scale(1.2) rotate(3deg);
/* background: var(--primary-color-5); */
transition: 0.5s;
}
