.TuduSearchDiv {
    margin-top: 30px;
    width: 100%;
    display: flex;
    padding: 100px 0px 0 0;
    flex-direction: column;
    align-items: center;
    color: var(--primary-color-3);
}
.TuduSearchInput{
    color: var(--text-color-placeholder);
    border:1px solid var(--primary-color-3);
    /* width: 50%; */
    padding: 15px 0px;
    border-radius: 5px;
    font-size: 1.4rem;
    font-weight: 500;
    /* color: #1E1E1F; */
    text-shadow: none;
    background: var(--faq-color);
    /* box-shadow: transparent; */
    /* box-shadow: 0 0 5px #e806a8, 0 0 20px #db0be2, 0 0 50px #1b5ace,
    0 0 100px #e6fafd; */
    /* transition: 0.2s ease; */
    /* user-select: none; */
    text-align: center;
    align-items: center;
    width: clamp(250px, 80%, 500px);

}

.TuduSearchInput:hover{
    border: 1px solid var(--primary-color-4);
}
.TuduSearchInput:focus{
    color: var(--primary-color-3);
    /* background: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% ); */
    border: 1px solid var(--primary-color-1);
