#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  

}
* {
  box-sizing: border-box;
}

body {
  font-family: montserrat;


}
body, div, ul, li, a, h1, h2, h3, h4, p{
  margin: 0;
  padding: 0;
}
body {
  --save-color: radial-gradient( circle farthest-corner at 10% 20%,  rgb(255, 94, 164) 17.8%, rgb(255, 2, 2) 100.2% );
  --text-color: #393E48;
  --primary-color-1:#F364F5;
  --primary-color-2:rgba(2, 154, 255, 0.175);
  --primary-color-3:#FEFEFE;
  --primary-color-4:#3b78fc;
  --primary-color-5: radial-gradient( circle farthest-corner at 70% 10%,  rgb(206, 24, 197) 17.8%, rgb(2, 57, 255) 100.2% );
  --dark-color: #171717;
  --text-color-dark: #333333;
  --text-color-white: #fff;
  --text-placeholder: rgba(149, 148, 148, 0.853);
  --faq-color: rgb(28,28,30);

  --bg-image: url("../public/background-anime1.jpg");

  /* background-image: var(--bg-image); */

  background: var(--dark-color);
  background-repeat: no-repeat, repeat;
  background-position:center;
  /* backdrop-filter:  brightness(5%); */
  width: 100%;
  /* height: 100%; */

  background-repeat: no-repeat;
  position: relative;
  /* width: 100%; */
  /* height: 1490px; */
  margin-left: auto;
  margin-right: auto;
}